<template><section><h1>dropDown 下拉菜单</h1>
<hr>
<h3>基础用法</h3>
<p>展示一组折叠的下拉菜单。需要配合 DropdownMenu 和 DropdownItem</p>
<dropdown>
  <span class="text">下拉菜单</span>
  <dropdown-menu>
    <dropdown-item>狮子</dropdown-item>
    <dropdown-item>老虎</dropdown-item>
    <dropdown-item disabled>大象</dropdown-item>
    <dropdown-item>猴子</dropdown-item>
  </dropdown-menu>
</dropdown>
<demo-block>
                        <div slot="desc"><html><head></head><body><dropdown>
  <span class="text">下拉菜单</span>
  <dropdown-menu>
    <dropdown-item>狮子</dropdown-item>
    <dropdown-item>老虎</dropdown-item>
    <dropdown-item disabled>大象</dropdown-item>
    <dropdown-item>猴子</dropdown-item>
  </dropdown-menu>
</dropdown>
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">dropdown</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"text"</span>&gt;</span>下拉菜单<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-menu</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>狮子<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>老虎<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span> <span class="hljs-attr">disabled</span>&gt;</span>大象<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>猴子<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-menu</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown</span>&gt;</span>
</code></pre>
</div></demo-block>
<hr>
<h3>触发方式</h3>
<p>通过设置属性 <code v-pre="" class="code_inline">trigger</code> 可以更改触发方式，可选项为 click 、 hover(默认)</p>
<dropdown trigger="click">
  <span class="text">点击下拉菜单</span>
  <dropdown-menu>
    <dropdown-item>狮子</dropdown-item>
    <dropdown-item>老虎</dropdown-item>
    <dropdown-item disabled>大象</dropdown-item>
    <dropdown-item>猴子</dropdown-item>
    <dropdown-item>熊</dropdown-item>
  </dropdown-menu>
</dropdown>
<demo-block>
                        <div slot="desc"><html><head></head><body><dropdown trigger="click">
  <span class="text">点击下拉菜单</span>
  <dropdown-menu>
    <dropdown-item>狮子</dropdown-item>
    <dropdown-item>老虎</dropdown-item>
    <dropdown-item disabled>大象</dropdown-item>
    <dropdown-item>猴子</dropdown-item>
    <dropdown-item>熊</dropdown-item>
  </dropdown-menu>
</dropdown></body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">dropdown</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"click"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"text"</span>&gt;</span>点击下拉菜单<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-menu</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>狮子<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>老虎<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span> <span class="hljs-attr">disabled</span>&gt;</span>大象<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>猴子<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span>&gt;</span>熊<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-menu</span>&gt;</span>
</code></pre>
</div></demo-block>
<hr>
<h3>指令事件</h3>
<p>点击菜单项后会触发事件，用户可以通过相应的菜单项 key 进行不同的操作</p>
<dropdown trigger="click" @command="handlerCommand">
  <span class="text">点击下拉菜单</span>
  <dropdown-menu>
    <dropdown-item command="lion">狮子</dropdown-item>
    <dropdown-item command="elephant" disabled>大象</dropdown-item>
    <dropdown-item command="monky">猴子</dropdown-item>
    <dropdown-item command="bear">熊</dropdown-item>
  </dropdown-menu>
</dropdown>
<demo-block>
                        <div slot="desc"><html><head></head><body><dropdown trigger="click" @command="handlerCommand">
  <span class="text">点击下拉菜单</span>
  <dropdown-menu>
    <dropdown-item command="lion">狮子</dropdown-item>
    <dropdown-item command="elephant" disabled>大象</dropdown-item>
    <dropdown-item command="monky">猴子</dropdown-item>
    <dropdown-item command="bear">熊</dropdown-item>
  </dropdown-menu>
</dropdown>
</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">dropdown</span> <span class="hljs-attr">trigger</span>=<span class="hljs-string">"click"</span> <span class="hljs-attr">@command</span>=<span class="hljs-string">"handlerCommand"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"text"</span>&gt;</span>点击下拉菜单<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-menu</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"lion"</span>&gt;</span>狮子<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"elephant"</span> <span class="hljs-attr">disabled</span>&gt;</span>大象<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"monky"</span>&gt;</span>猴子<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dropdown-item</span> <span class="hljs-attr">command</span>=<span class="hljs-string">"bear"</span>&gt;</span>熊<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">dropdown-menu</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">dropdown</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2>API</h2>
<h4>Dropdown props</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>trigger</td>
<td>触发方式</td>
<td>string</td>
<td>hover,click</td>
<td>hover</td>
</tr>
<tr>
<td>hide-on-click</td>
<td>是否在点击菜单项后隐藏菜单</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>show-timeout</td>
<td>展开下拉菜单的延时（仅在 trigger 为 hover 时有效）</td>
<td>number</td>
<td>—</td>
<td>200</td>
</tr>
<tr>
<td>hide-timeout</td>
<td>收起下拉菜单的延时（仅在 trigger 为 hover 时有效）</td>
<td>number</td>
<td>—</td>
<td>200</td>
</tr>
</tbody>
</table>
<h4>Dropdown event</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>command</td>
<td>点击菜单项触发的事件回调</td>
<td>dropdown-item 的指令</td>
</tr>
<tr>
<td>visible-change</td>
<td>下拉框出现/隐藏时触发</td>
<td>出现则为 true，隐藏则为 false</td>
</tr>
</tbody>
</table>
<h4>Dropdown Menu Item Attributes</h4>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>command</td>
<td>指令</td>
<td>string/number/object</td>
<td>—</td>
<td></td>
</tr>
<tr>
<td>disabled</td>
<td>禁用</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>


</section></template>
<style>
  .max-dropdown {
    margin: 20px 0;
  }
  .text {
    font-weight: 600;
    color: #2d8cf0;
  }
</style>
<script>
export default {
    methods: {
      handlerCommand(e) {
        alert(e)
      }
    }
}
</script>


